<template>
  <div>
    <nav-bar title="收货地址"></nav-bar>
    <div class="list" v-if="defaultItem || addressList.length > 0">
      <div class="default-item">
        <div class="left">
          <div class="icon-box">
            <van-icon name="wap-home" size="25" color="#fff" />
          </div>
          <div class="info">
            <div class="top">
              <span class="name">{{ defaultItem.name }}</span>
              <div class="mobile">{{ defaultItem.phone }}</div>
              <div class="default">默认</div>
            </div>
            <div class="bot">{{ defaultItem.region && defaultItem.region.province + defaultItem.region.city + defaultItem.region.region + defaultItem.detail }}</div>
          </div>
        </div>
        <van-icon size="23" name="edit" @click="$router.replace(`/editAddress?mode=edit&addressId=${defaultItem.address_id}`)" />
      </div>
      <div class="item" v-for="item in addressList" :key="item.address_id">
        <div class="left">
          <div class="icon-box" v-if="item.address_id === defaultId">
            <van-icon name="wap-home" size="25" color="#fff" />
          </div>
          <div class="name-box" v-else>
            <div class="name">{{ item.name.slice(0,1) }}</div>
          </div>
          <div class="info">
            <div class="top">
              <span class="name">{{ item.name }}</span>
              <div class="mobile">{{ item.phone }}</div>
            </div>
            <div class="bot">{{ item.region.province + item.region.city + item.region.region + item.detail }}</div>
          </div>
        </div>
        <van-icon size="23" name="edit" @click="$router.replace(`/editAddress?mode=edit&addressId=${item.address_id}`)" />
      </div>
    </div>
    <div class="empty-box" v-else>
      <van-empty description="暂无收货地址" />
    </div>
    <div class="btn-box">
      <button class="btn" @click="$router.replace('/editAddress?mode=add')">
        <van-icon name="plus" size="18" />
        <span>添加收货地址</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'addressList',
  data () {
    return {
    }
  },
  computed: {
    defaultItem () {
      return this.$store.getters['address/defaultItem']
    },
    addressList () {
      return this.$store.getters['address/addressList']
    },
    defaultId () {
      return this.$store.state.address.defaultId
    }
  },
  created () {
    this.$store.dispatch('address/getAddressList')
  }
}
</script>

<style lang="less" scoped>
.list{
  padding: 0 15px 65px;
  .default-item,.item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 0;
    .left{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      width: 90%;
      .icon-box,.name-box{
        width: 35px;
        height: 35px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .icon-box{
        background-color: rgb(255, 145, 0);
      }
      .name-box{
        background-color: rgb(252, 198, 162);
        color: #fff;
        font-size: 18px;
      }
      .info{
        margin-left: 12px;
        flex: 1;
        .top{
          display: flex;
          align-items: center;
          justify-content: flex-start;
          font-size: 15px;
          .name{
            font-size: 16px;
            font-weight: bold;
          }
          .mobile{
            font-size: 14px;
            margin-left: 5px;
          }
          .default{
            color: orangered;
            font-size: 12px;
            padding:0 5px;
            border: 1px solid orangered;
            border-radius: 5px;
            margin-left: 8px;
          }
        }
        .bot{
          margin-top: 5px;
          font-size: 13px;
          color: #666;
        }
      }
    }
  }
}
.btn-box{
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  padding: 10px;
  background-color: #fff;
  .btn{
    width: 100%;
    height: 45px;
    background: linear-gradient(to right, #ff6034, #ee0a24);
    color: #fff;
    font-size: 16px;
    border-radius: 30px;
    span{
      margin-left: 10px;
    }
  }
}
.van-empty{
  height: calc(100vh - 200px);
}
</style>
